"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface LocationData {
  latitude: number;
  longitude: number;
}

export default function EmergencyButton() {
  const [isProcessing, setIsProcessing] = useState(false);
  const [status, setStatus] = useState<{
    type: "success" | "error" | "info" | null;
    message: string;
  }>({ type: null, message: "" });

  const getLocation = (): Promise<LocationData> => {
    return new Promise((resolve, reject) => {
      if (!navigator.geolocation) {
        reject(new Error("Geolocation is not supported by this browser"));
        return;
      }

      navigator.geolocation.getCurrentPosition(
        (position) => {
          resolve({
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
          });
        },
        (error) => {
          let errorMessage = "Unable to get location";
          switch (error.code) {
            case error.PERMISSION_DENIED:
              errorMessage = "Location access denied. Please enable location services and try again.";
              break;
            case error.POSITION_UNAVAILABLE:
              errorMessage = "Location information unavailable.";
              break;
            case error.TIMEOUT:
              errorMessage = "Location request timed out.";
              break;
          }
          reject(new Error(errorMessage));
        },
        {
          enableHighAccuracy: true,
          timeout: 10000,
          maximumAge: 60000,
        }
      );
    });
  };

  const sendEmergencyAlert = async () => {
    setIsProcessing(true);
    setStatus({ type: "info", message: "Getting your location..." });

    try {
      // Get user's location
      const location = await getLocation();
      
      setStatus({ type: "info", message: "Sending emergency alert..." });

      // Send emergency alert to API
      const response = await fetch("/api/emergency", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          latitude: location.latitude,
          longitude: location.longitude,
          timestamp: new Date().toISOString(),
        }),
      });

      if (response.ok) {
        const data = await response.json();
        setStatus({
          type: "success",
          message: `Emergency alert sent successfully! ${data.volunteersNotified || 0} volunteers and ${data.policeStationsNotified || 0} police stations have been notified. Help is on the way.`,
        });
      } else {
        const errorData = await response.json();
        setStatus({
          type: "error",
          message: errorData.message || "Failed to send emergency alert. Please try again or call emergency services directly.",
        });
      }
    } catch (error) {
      if (error instanceof Error) {
        setStatus({
          type: "error",
          message: error.message,
        });
      } else {
        setStatus({
          type: "error",
          message: "An unexpected error occurred. Please try again or call emergency services directly.",
        });
      }
    } finally {
      setIsProcessing(false);
    }
  };

  const handleEmergencyClick = () => {
    // Show confirmation before sending alert
    const confirmed = window.confirm(
      "Are you sure you want to send an emergency alert? This will notify volunteers and police stations in your area."
    );
    
    if (confirmed) {
      sendEmergencyAlert();
    }
  };

  return (
    <div className="space-y-6">
      <div className="text-center">
        <Button
          onClick={handleEmergencyClick}
          disabled={isProcessing}
          size="lg"
          className="w-64 h-64 rounded-full bg-red-600 hover:bg-red-700 text-white text-2xl font-bold shadow-lg hover:shadow-xl transition-all duration-200 transform hover:scale-105"
        >
          {isProcessing ? (
            <div className="flex flex-col items-center space-y-2">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-white"></div>
              <span className="text-lg">Processing...</span>
            </div>
          ) : (
            <div className="flex flex-col items-center space-y-2">
              <span className="text-4xl">🚨</span>
              <span>EMERGENCY</span>
              <span className="text-lg">ALERT</span>
            </div>
          )}
        </Button>
      </div>

      {status.type && (
        <Alert 
          className={
            status.type === "success" 
              ? "border-green-500 bg-green-50" 
              : status.type === "error"
              ? "border-red-500 bg-red-50"
              : "border-blue-500 bg-blue-50"
          }
        >
          <AlertDescription 
            className={
              status.type === "success" 
                ? "text-green-700" 
                : status.type === "error"
                ? "text-red-700"
                : "text-blue-700"
            }
          >
            {status.message}
          </AlertDescription>
        </Alert>
      )}

      <div className="bg-yellow-50 p-4 rounded-lg">
        <h3 className="font-semibold text-yellow-900 mb-2">Important Instructions:</h3>
        <ul className="space-y-1 text-sm text-yellow-800">
          <li>• Only use this button in genuine emergencies</li>
          <li>• Allow location access when prompted for faster response</li>
          <li>• Stay calm and wait for help to arrive</li>
          <li>• If possible, move to a safe location</li>
          <li>• For immediate danger, also call local emergency services (100/112)</li>
        </ul>
      </div>
    </div>
  );
}
